/* Header.css */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: var(--primary-color);
    color: var(--text-color);
  }
  
  .header-logo {
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .header-nav ul {
    list-style-type: none;
    display: flex;
    gap: 1rem;
    margin: 0;
    padding: 0;
  }
  
  .header-nav a {
    color: white;
    text-decoration: none;
  }
  
  .header-nav a:hover {
    text-decoration: underline;
  }
  